import React from "react";
import '../common/styles/main.css';

const MainJackpot = (props) => {
    const imgLink = "http://win-cor.winbet-bg.com/storage/" + props.data.front_file_name;
    console.log(props.data.front_file_name);
  return (
    <div className="slider-overlay">
      <div className={props.data[props.description] === null ? "overlay-content text-center" : "overlay-content"}>
        <h2>{props.data[props.title]}</h2>
        <img className={props.data.front_file_name === null ? "emty-slider-front-img" : "slider-front-img"} src={imgLink} />
        <div className="title-white-line"></div>
        <p>{props.data[props.description]} 
        </p>
        <a target="_blank" rel="noopener noreferrer" href={props.data.button_1_bg_url} className={props.data[props.button1] === null ? 'slider-register-emty' : 'slider-register'}><span>{props.data[props.button1]}</span></a>
        <a target="_blank" rel="noopener noreferrer" href={props.data.button_2_bg_url} className={props.data[props.button2] === null ? 'slider-log-in-emty' : 'slider-log-in'}><span>{props.data[props.button2]}</span></a>
      </div>
    </div>
  );
}


export default MainJackpot;



